/* Import Fonts */
@import url('https://fonts.googleapis.com/css2?family=Fredoka:wght@400;600&family=Mochiy+Pop+One&display=swap');

/* Global Style */
body {
  margin: 0;
  background: radial-gradient(ellipse at bottom, #223344 0%, #090a0f 100%);;
  color: aliceblue;
  font-family: 'Fredoka', sans-serif;
  overflow: hidden;
}

a{
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 10rem;
  animation: slide-right 0.8s cubic-bezier(0.175, 0.885, 0.320, 1.275) both;
}
.back-btn{
  width: 20rem;
  height: 3rem;
  margin-top: 3rem;
  color: aliceblue;
  background-color: transparent;
  border-top-left-radius: 15px;
  border: 1px solid white;
  position: absolute;
  left: 1rem;
  top:0;
  font-family: "Mochiy Pop One", monospace;
  font-size: 1rem;
}
.back-btn:hover{
   background-color: white;
   color: black;
}
.back-btn span{
  font-size: 3rem;
  position: absolute;
  left: 4rem;
  top: 0;
}
/* Footer */

.footer {
  margin-top: 2rem;
  text-align: center;
}
/* Media Query */
@media only screen and (max-width: 768px) {
  .back-btn{
    width: 15rem;
  }
  .back-btn span{
    left: 2rem;
  }
}
/* KeyFrames */
@keyframes slide-right {
  0% {
    transform: translateX(-100px);
  }
  100% {
    transform: translateX(0);
  }
}