/* Portolio / Home */
.App {
  text-align: center;
  color:aliceblue;
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  animation: slide-bottom 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
/* Bagian Kepala Portfolio */
.head {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.App .side-img{
  width: 30vw;
  height: 60vh;
  position: absolute;
  bottom: 0;
  left: 4rem;
  background: url("../../../public/img/Miku.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.head .profile{
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  overflow: hidden;
  background-image: url("../../../public/img/profile.jpg");
  background-position: 10px 30%;
  background-size: cover;
}
.head img {
  width: 4rem;
  /* height: 100px; */
}
/* Bagian List Button Portfolio */
.item-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item-list button{
  width: 20rem;
  height: 3rem;
  margin-top: 3rem;
  color: aliceblue;
  background-color: transparent;
  border: 1px solid white;
  transform: skewY(5deg);
}
.item-list button:hover{
  background-color: white;
  color: black;
}
/* Media Query */
@media only screen and (max-width: 768px) {
  .App .side-img{
    visibility: hidden;
  }
}
/* Animation Key */
@keyframes slide-bottom {
  0% {
    transform: translateY(-100px);
  }
  100% {
    transform: translateY(0);
  }
}

