/* Sosmed List */
.sosmed-list{
    display: flex;
    align-items: center;
    flex-direction: column;
    text-indent: 0;
    text-align: center;
    list-style: none;
}
.item-sosmed-list{
    display:block;
    border-radius: 3px;
    width: 10rem;
    height: 2rem;
    padding: 5px;
    margin: 1rem;
    background-color: rgb(51, 51, 80);
    color: white;
}
.item-sosmed-list:hover{
    color: black;
    background-color: aliceblue;
}