.Card{
    width: 50%;
    min-height: 400px;
    /* border: 1px solid #F2F4CB; */
    box-shadow:  #76DC91;
    text-align: center;
}
hr {
    width: 40%;
    margin-top: -1rem;
    border-color: #76DC91;
}
.card-body{
    margin-top: 2rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    line-height: 2rem;
    text-align: start;
    text-indent: 3rem;
}

@media only screen and (max-width: 768px) {
    .Card{
        width: 80%;
    }
}